.container {
  display: flex;
  flex-direction: row;
  background-position: center;
  background-color: #142241;
  background-size: cover;
  border-radius: 20px;
  padding: 50px;
  margin: 10px;
  gap: 20px;
  align-items: center;
  justify-content: flex-start;
}

.user_icon {
  width: 130px;
}

.user_info {
  display: flex;
  flex-direction: column;
  padding-left: 10px;
  gap: 10px;
}

.title {
  color: white;
  font-size: 30px;
  font-weight: bold;
  text-shadow: rgba(0, 0, 0, 0.25) 2px 5px 3px;
  margin: 0;
}

.mail {
  margin: 0;
  color: white;
  font-size: 15px;
  font-weight: lighter;
  text-shadow: rgba(0, 0, 0, 0.25) 2px 5px 3px;
}

.stats_wrapper {
  display: flex;
  flex-direction: row;
  gap: 40px;
}

.user_stats {
  margin-top: 15px;
  gap: 5px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.stats_title {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  color: white;
}

.profit,
.loss {
  margin: 0;
  font-size: 15px;
  font-weight: lighter;
  color: white;
}

.profit {
  color: #57d054;
}

.loss {
  color: #ed476e;
}

.edit_button {
  border-radius: 10px;
  border: none;
  background-color: #d4af37;
  transition: 0.5s;
}

.edit_button:hover {
  transform: scale(1.15);
}

@media screen and (max-width: 800px) {
  .container {
    flex-direction: column;
  }
}
